.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(56, 44, 39, 0.8);
  // display: flex;
  display: none;
  z-index: 100;

  &__dialog {
    max-width: 970px;
    width: 98%;
    height: 470px;
    margin: auto;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    position: relative;
    background-color: #fafafa;
    overflow: hidden;
  }

  &__content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

  }


  &__wrap {
    flex-basis: 50%;
    margin: 0;
    max-width: 470px;
    width: 98%;
    height: 470px;
    position: relative;
    overflow: hidden;
  }

  &__img {
    height: 100%;
    display: block;
    object-fit: cover;
    margin: 0 auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

  }

  &__info {
    flex-basis: 47%;
    padding: 40px 30px;
  }

  &__name {
    font-family: $title-ff;
    font-size: 3.5rem;
    font-weight: 400;
    letter-spacing: 2px;
    margin: 0;
  }

  &__breed {
    font-size: 2rem;
    font-family: $title-ff;
    font-weight: 400;
    margin: 0;
  }

  &__description {
    max-height: 125px;
    overflow: hidden;
  }

  &__list {
    width: 300px;
    padding: 0 20px;
    color: $text-color;
    font-size: 1.7rem;
    font-family: $title-ff;
  }

  &__data {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  &__item {
    list-style: none;
    position: relative;

    &:before {
      content: '';
      display: block;
      width: 5px;
      height: 5px;
      background-color: $brand-color;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: -5%;
      transform: translate(0, -50%);
    }
  }


  &__close {
    outline: none;
    display: block;
    width: 50px;
    height: 50px;
    font-size: 3rem;
    line-height: 20px;
    color: #292929;
    padding: 14.5px;
    border: $b-size solid $brand-color;
    background-color: transparent;
    border-radius: 50%;
    cursor: pointer;
    position: absolute;
    top: 5%;
    right: 3%;
    text-align: center;
    align-items: center;

    @include hover {
      background-color: $brand-color;
    }
  }

}

.opened {
  display: flex;
}