.slider {
  margin: 60px auto;
  max-width: 915px;

  position: relative;

  &__arrow {
    z-index: 10;
    width: 50px;
    height: 50px;
    background-color: transparent;
    outline: none;
    border: none;
    border: $b-size solid $brand-color;
    border-radius: 50%;
    padding: 15px;
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);

    &--left {
      left: -13%;
    }

    &--right {
      right: -13%;
    }

    &:hover {
      background-color: $brand-color;
    }
  }
}

.card {
  width: 270px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 30px;
  ;
  background-color: #fafafa;
  border-radius: 9px;
  overflow: hidden;


  @include hover {
    box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.04)
  }

  &__img-wrap {
    width: 100%;
    height: 270px;
    overflow: hidden;
    position: relative;
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: block;
    @include pCenter;

  }

  &__name {
    font-family: $title-ff;
    font-size: 2rem;
    letter-spacing: 1px;

    max-width: 180px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    margin: 15px 0 10px;
  }

}