@import "normalize";
@import "variables";
@import "mixins";
@import "pets";
@import "modal";
@import "slider";

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  transition: all 025s ease-in-out;
  outline: none;
}

html {
  height: 100%;
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  @include font(1.5rem, $text-color, 400, $text-ff, 1.6);
}

h2 {
  margin: 0;
  font-size: 3.5rem;
  font-family: $title-ff;
  color: $title-color;
  font-weight: normal;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 2px;
}

.container {
  max-width: 1170px;
  width: 95%;
  margin: 0 auto;

  &--about {
    max-width: 870px;
  }
}


.section {
  padding: 70px 0;

  &--head {
    min-height: 100vh;
    background: url(../img/img1.png) right -100px bottom -40px / 60% no-repeat,
      url(../img/background.jpg) 0 0 / cover no-repeat;
  }

  &--bg {
    background-color: #f6f6f6;
  }
}

// Header
.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  padding-top: 45px;
  padding-bottom: 10px;
  transition: all 0.5s ease-in;

  &--dark {
    position: initial;
    padding: 30px 0;
  }
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    font-size: 3.2rem;
    font-family: $title-ff;
    color: $brand-color;
    line-height: 1.2;

    &--dark {
      color: #545454;
    }
  }

  &__subtitle {
    font-size: 1.3rem;
    color: #fff;
    letter-spacing: 0.7px;

    &--dark {
      color: #292929;
    }
  }
}

.menu {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;

  &__item {
    list-style: none;

    &:not(:last-child) {
      margin-right: 35px
    }
  }

  &__link {
    color: $header-color;
    padding: 5px 0;
    border-bottom: 3px solid transparent;

    @include hover {
      color: #dfdfdf;
      border-color: $brand-color;
    }

    &--dark {
      color: #545454;

      @include hover {
        color: #292929;
      }
    }
  }
}

// End of Header 

// Buttons
.btn {
  outline: none;
  display: block;
  color: #292929;
  line-height: 1;
  letter-spacing: 1px;
  padding: 15px 40px;
  border-radius: 30px;
  font-family: $title-ff;
  text-align: center;
  cursor: pointer;
  background-color: $brand-color;
  border: $b-size solid $brand-color;

  @include hover {
    background-color: transparent;
  }

  &--welcome {
    max-width: 190px;

    @include hover {
      background-color: transparent;
      color: $brand-color;
    }
  }

  &--primary {
    background-color: transparent;

    @include hover {
      background-color: $brand-color;
    }
  }

  &--centered {
    max-width: 240px;
    margin: 0 auto;
  }
}

// End of Buttons

// Welcome 
.welcome {
  margin-top: 200px;
  max-width: 450px;

  &__title {
    font-size: 4.4rem;
    font-family: $title-ff;
    color: #ffffff;
    margin: 0;
    line-height: 1.3;
    font-weight: normal;
  }

  &__text {
    color: $header-color;
    margin: 35px 0;

  }
}

// About 
.about {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;

  &__img {
    display: block;
    width: 100%;
    flex-basis: 45%;
    transform: scale(0.7);
  }

  &__texts {
    flex-basis: 55%;
    padding: 0 20px;
  }

  &__title {
    text-align: left;
  }

  &__text {
    color: $text-color;

    &:not(:last-child) {
      margin: 40px 0 30px 0
    }
  }
}

.help {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 50px;

  &__item {
    min-width: 150px;
    min-height: 110px;
    margin: 30px 30px;
    position: relative;
  }

  &__img {
    display: block;
    margin: 0 auto;
  }

  &__text {
    font-family: $title-ff;
    min-width: 150px;
    font-size: 2rem;
    text-align: center;
    line-height: 1;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, 0);
  }
}

.donation {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  &__img-wrap {
    flex-basis: 45%;
  }

  &__img {
    width: 100%;
  }

  &__info {
    margin-left: 65px;
    flex-basis: 37%;
  }

  &__title {
    text-align: left;
  }

  &__card-name {
    font-family: $title-ff;
    display: inline-block;
    letter-spacing: 1.2px;
    margin: 25px 0 10px 0;

  }

  &__card-number {
    background-color: $brand-color;
    border-radius: 9px;
    max-width: 330px;
    font-family: 'Times New Roman', Times, serif;
    letter-spacing: 1.5px;
    line-height: 1;
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 12px 15px;
  }

  &__legal {
    flex: 1.2rem;
    color: #b2b2b2;
    font-style: italic;
    line-height: 1.5;
    margin-top: 30px;
  }

  & .fas {
    margin-right: 10px;
  }
}

.footer {
  padding: 70px 0;
  min-height: 40vh;
  background: url(../img/img7.png) right 150px bottom -20px no-repeat,
    url(../img/background2.jpg) 0 0 / cover no-repeat;


  &__wrap {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__block {
    flex-basis: 28%;
    min-height: 230px;
    margin-right: 130px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
    color: #ffffff;
    text-align: left;
  }

  &__icon {
    margin-right: 20px;
  }

  &__link {
    color: $brand-color;
    font-family: 'Times New Roman', Times, serif;
    font-size: 2rem;
    letter-spacing: 1.5px;
    line-height: 1.2;

    @include hover {
      color: #fff;
    }
  }

  &__item {
    display: flex;
    align-items: center;
  }
}

.friends {
  display: flex;
  justify-content: space-around;

  margin: 60px auto 40px;
  flex-wrap: wrap;

  & .card {
    margin-bottom: 30px;
  }
}

.pagination {
  width: 70%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  &__item {
    outline: none;
    cursor: pointer;
    display: block;
    width: 50px;
    height: 50px;
    background-color: transparent;
    border: 2px solid $brand-color;
    border-radius: 50%;
    margin-right: 15px;
    margin-top: 15px;
    position: relative;

    &:hover {
      background-color: $brand-color;
    }
  }

  &__number {
    @include pCenter;
    color: #292929;
  }

  & .active {
    background-color: $brand-color;

  }

  &__dots {
    margin-right: 15px;
    display: inline-block;
    vertical-align: middle;
    padding: 12px 0;
  }

  &__dot {
    display: inline-block;
    width: 5px;
    height: 5px;
    background-color: $brand-color;
    border-radius: 50%;
    margin: 0 0.5px;
  }
}




@media screen and (max-width: 1250px) {

  .section {
    &--head {
      background: url(../img/img1.png) right -200px bottom 0px/70% no-repeat,
        url(../img/background.jpg) 0 0/cover no-repeat;

    }
  }

  .slider {
    &__arrow {
      display: none;
    }
  }

  .footer {
    background: url(../img/img7.png) right 10px bottom 0px no-repeat,
      url(../img/background2.jpg) 0 0/cover no-repeat;

    &__block {
      flex-basis: 35%;
      margin-right: 20px;
    }
  }
}

@media screen and (max-width: 992px) {

  .container {
    max-width: 800px;

    &--about {
      max-width: 770px;
    }
  }

  .section {
    &--head {
      background: url(../img/img1.png) right -200px bottom 0px/80% no-repeat,
        url(../img/background.jpg) 0 0/cover no-repeat;

    }
  }

  .welcome {
    max-width: 350px;

    &__text {
      font-size: 1.7rem;
    }
  }

  .donation {
    flex-direction: column;
    width: 80%;
    margin: 0 auto;

    &__title {
      text-align: center;
    }

    &__info {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin: 30px 0px 0px 0px;
    }
  }

  .footer {
    background: url(../img/background2.jpg) 0 0/cover no-repeat;

    &__wrap {
      justify-content: space-between;

    }



    &__block {
      flex-basis: 45%;
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 767px) {

  .hamburger {
    position: absolute;
    top: 0;
    right: 7%;
    z-index: 1100;
    position: fixed;
    cursor: pointer;
  }

  #hamburger-icon {
    width: $width-line;
    height: $height-icon;
    position: relative;
    display: block;
    margin: ($height-icon * 2) auto $height-icon auto;

    .line {
      display: block;
      background: $white;
      width: $width-line;
      height: $height-line;
      position: absolute;
      left: 0;
      border-radius: ($height-line / 2);
      transition: all $transition-time;
      -webkit-transition: all $transition-time;
      -moz-transition: all $transition-time;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);

      &.line-1 {
        top: 0;
      }

      &.line-2 {
        top: 50%;
      }

      &.line-3 {
        top: 100%;
      }
    }

    &:hover,
    &:focus {
      .line-1 {
        transform: translateY($height-line / 2 * -1);
        -webkit-transform: translateY($height-line / 2 * -1);
        -moz-transform: translateY($height-line / 2 * -1);
      }

      .line-3 {
        transform: translateY($height-line / 2);
        -webkit-transform: translateY($height-line / 2);
        -moz-transform: translateY($height-line / 2);
      }
    }

    &.active {
      .line-1 {
        transform: translateY($translateY) translateX($translateX) rotate($rotation);
        -webkit-transform: translateY($translateY) translateX($translateX) rotate($rotation);
        -moz-transform: translateY($translateY) translateX($translateX) rotate($rotation);
      }

      .line-2 {
        opacity: 0;
      }

      .line-3 {
        transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
        -webkit-transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
        -moz-transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
      }
    }
  }

  .container {
    width: 90%;

    &--about {
      max-width: 770px;
    }
  }

  .section {
    padding: 50px 0;
  }

  .header {
    position: relative;
    padding: 0;
    height: 0;

    &--dark {
      height: 150px;
    }
  }

  .nav {
    flex-direction: column;

  }

  .logo {
    margin-top: 50px;
  }

  .menu {
    width: 100%;
    height: 350px;
    font-size: 3rem;
    padding: 80px 0;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    text-align: center;
    position: absolute;
    position: fixed;
    top: -70%;
    transition: all 0.7s ease-in-out;

    background-color: rgba(56, 44, 39, 1);

    &__item {
      // margin: 0;

      &:not(:last-child) {
        margin: 0;
      }
    }

    &__link {
      &--dark {
        color: $header-color;

        @include hover {
          color: #dfdfdf;
        }

      }
    }
  }

  .welcome {
    margin-top: 150px;
  }

  .about {
    flex-direction: column;

    &__texts {
      order: 1;
    }

    &__title {
      text-align: center;
    }

    &__img {
      order: 2;
      width: 60%;
    }
  }

  .footer {
    &__wrap {
      flex-direction: column;
      width: 90%;
      margin: 0 auto;
    }

    &__block {
      &:nth-child(2) {
        margin-top: 50px;
      }
    }

    &__title {
      text-align: center;
      font-size: 3rem;
    }
  }
}

@media screen and (max-width: 576px) {

  h2 {
    font-size: 3rem;
  }

  .section {
    &--head {
      background: url(../img/img1.png) right -100px bottom 0px/82% no-repeat,
        url(../img/background.jpg) 0 0/cover no-repeat;

    }
  }

  .help {
    &__item {
      margin: 10px;
    }
  }

  .donation {
    &__card-number {
      font-size: 1.6rem;
    }
  }

  .modal {
    &__content {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 320px) {}